import { useEffect, useState, useMemo, useCallback, useRef } from 'react'
import { useParams, useLocation } from 'react-router-dom'
import { Typography, Spin, Alert, Anchor, Image } from 'antd'
import ReactMarkdown from 'react-markdown'
import remarkGfm from 'remark-gfm'
import rehypeRaw from 'rehype-raw'
import rehypeSanitize, { defaultSchema } from 'rehype-sanitize'
import CodeBlock from '@/components/CodeBlock'

//  schema style 
const customSchema = {
  ...defaultSchema,
  attributes: {
    ...defaultSchema.attributes,
    '*': [...(defaultSchema.attributes?.['*'] || []), 'style', 'className'],
  },
}
import matter from 'gray-matter'
import PageNavigation from '@/components/PageNavigation'
import { getPageNavigation } from '@/utils/pageOrder'
import { useSidebar } from '@/contexts/SidebarContext'
import { useTheme } from '@/contexts/ThemeContext'
import { SIDEBAR_COLLAPSED_WIDTH, SIDEBAR_WIDTH } from '@/constants'

const { Title } = Typography

interface MarkdownMeta {
  title: string
  titleEn?: string
  order?: number
}

interface MarkdownPageProps {
  slug?: string
}

interface TocItem {
  id: string
  text: string
  level: number
}

// 
const extractToc = (content: string): TocItem[] => {
  const headingRegex = /^(#{2,4})\s+(.+)$/gm
  const toc: TocItem[] = []
  let match

  while ((match = headingRegex.exec(content)) !== null) {
    const level = match[1].length
    const text = match[2].trim()
    const id = text
      .toLowerCase()
      .replace(/[^\u4e00-\u9fa5a-z0-9]+/g, '-')
      .replace(/^-+|-+$/g, '')

    toc.push({ id, text, level })
  }

  return toc
}

const MarkdownPage = ({ slug: propSlug }: MarkdownPageProps = {}) => {
  const { slug: paramSlug } = useParams<{ slug: string }>()
  const location = useLocation()
  const { collapsed } = useSidebar()
  const { isDark } = useTheme()
  const slug = propSlug || paramSlug
  const [content, setContent] = useState('')
  const [meta, setMeta] = useState<MarkdownMeta | null>(null)
  const [loading, setLoading] = useState(true)
  const [error, setError] = useState<string | null>(null)
  const [activeId, setActiveId] = useState<string>('')

  const sidebarWidth = collapsed ? SIDEBAR_COLLAPSED_WIDTH + 20 : SIDEBAR_WIDTH + 20

  // 
  const toc = useMemo(() => extractToc(content), [content])

  // 
  const pageNav = useMemo(() => getPageNavigation(location.pathname), [location.pathname])

  useEffect(() => {
    const loadMarkdown = async () => {
      if (!slug) {
        setError('')
        setLoading(false)
        return
      }

      try {
        setLoading(true)
        setError(null)
        //  markdown 
        const mdModule = await import(`../content/${slug}.md?raw`)
        const rawContent = mdModule.default
        //  frontmatter
        const { data, content: mdContent } = matter(rawContent)
        setMeta(data as MarkdownMeta)
        setContent(mdContent)
        setLoading(false)
      } catch (err) {
        setError(`: ${slug}`)
        setLoading(false)
      }
    }

    loadMarkdown()
  }, [slug])

  // 
  const scrollTimerRef = useRef<ReturnType<typeof setTimeout> | null>(null)

  const handleScroll = useCallback(() => {
    const headings = document.querySelectorAll('h2, h3, h4')
    let currentId = ''

    headings.forEach((heading) => {
      const rect = heading.getBoundingClientRect()
      if (rect.top <= 100 && rect.top >= -100) {
        currentId = heading.id
      }
    })

    if (currentId) {
      setActiveId(currentId)
    }
  }, [])

  const throttledHandleScroll = useCallback(() => {
    if (scrollTimerRef.current) return
    scrollTimerRef.current = setTimeout(() => {
      handleScroll()
      scrollTimerRef.current = null
    }, 100)
  }, [handleScroll])

  useEffect(() => {
    window.addEventListener('scroll', throttledHandleScroll)
    handleScroll() // 

    return () => {
      window.removeEventListener('scroll', throttledHandleScroll)
      if (scrollTimerRef.current) {
        clearTimeout(scrollTimerRef.current)
      }
    }
  }, [content, throttledHandleScroll, handleScroll])

  //  URL hash 
  useEffect(() => {
    if (!content || loading) return

    //  window.location  hash
    const hash = window.location.hash
    if (hash) {
      //  URL  hash
      const decodedHash = decodeURIComponent(hash.slice(1))
      //  DOM 
      const timer = setTimeout(() => {
        const element = document.getElementById(decodedHash)
        if (element) {
          //  scrollTo  offsetTop 
          const offsetTop = element.offsetTop
          window.scrollTo({ top: offsetTop, behavior: 'smooth' })
          setActiveId(decodedHash)
        }
      }, 300)

      return () => clearTimeout(timer)
    }
  }, [content, loading])

  if (loading) {
    return (
      <div className="flex items-center justify-center h-[calc(100vh-64px)]">
        <Spin size="large" />
      </div>
    )
  }

  if (error) {
    return (
      <div className="p-6 md:p-8 max-w-4xl mx-auto">
        <Alert message="" description={error} type="error" showIcon />
      </div>
    )
  }

  //  ID
  const generateHeadingId = (text: string) => {
    return text
      .toLowerCase()
      .replace(/[^\u4e00-\u9fa5a-z0-9]+/g, '-')
      .replace(/^-+|-+$/g, '')
  }

  return (
    <div className={`relative p-6 md:p-8 !pb-32 ${isDark ? 'bg-[#141414]' : 'bg-white'}`}>
      {/*  -  */}
      {toc.length > 0 && (
        <div className="hidden xl:block w-48 fixed top-24 max-h-[calc(100vh-120px)] overflow-y-auto transition-all duration-300" style={{ left: sidebarWidth }}>
          {/* <div className="text-sm font-medium text-gray-500 mb-2 pl-2"></div> */}
          <Anchor
            affix={false}
            targetOffset={80}
            getCurrentAnchor={() => `#${activeId}`}
            items={toc.map((item) => ({
              key: item.id,
              href: `#${item.id}`,
              title: (
                <span
                  className={`transition-colors duration-200 ${activeId === item.id ? 'text-[#F07D18] font-medium' : isDark ? 'text-gray-400 hover:text-[#F07D18]' : 'text-gray-600 hover:text-[#F07D18]'}`}
                  style={{
                    paddingLeft: item.level === 2 ? 0 : item.level === 3 ? 12 : 24,
                    fontSize: item.level === 2 ? 14 : 13,
                  }}
                >
                  {item.text}
                </span>
              ),
            }))}
            className="toc-anchor"
          />
          <style>{`
            .toc-anchor .ant-anchor-link {
              padding: 4px 0;
            }
            .toc-anchor .ant-anchor-link-title {
              font-size: 14px;
              transition: color 0.2s;
            }
            .toc-anchor .ant-anchor-ink::before {
              background-color: #F07D18;
            }
          `}</style>
        </div>
      )}

      {/*  */}
      <div className="markdown-content w-full max-w-4xl mx-auto px-8 text-base leading-relaxed">
        {meta?.title && (
          <Title level={1} className="!mb-8">
            {meta.title}
          </Title>
        )}
        <ReactMarkdown
          remarkPlugins={[remarkGfm]}
          rehypePlugins={[rehypeRaw, [rehypeSanitize, customSchema]]}
          components={{
            h1: ({ children }) => (
              <Title level={1} className="!mt-6 !mb-6 scroll-mt-20">
                {children}
              </Title>
            ),
            h2: ({ children }) => {
              const text = String(children)
              const id = generateHeadingId(text)
              return (
                <Title level={2} id={id} className="!mt-10 !mb-5 scroll-mt-20">
                  {children}
                </Title>
              )
            },
            h3: ({ children }) => {
              const text = String(children)
              const id = generateHeadingId(text)
              return (
                <Title level={3} id={id} className="!mt-8 !mb-4 scroll-mt-20">
                  {children}
                </Title>
              )
            },
            h4: ({ children }) => {
              const text = String(children)
              const id = generateHeadingId(text)
              return (
                <Title level={4} id={id} className="!mt-6 !mb-4 scroll-mt-20">
                  {children}
                </Title>
              )
            },
            h5: ({ children }) => {
              const text = String(children)
              const id = generateHeadingId(text)
              return (
                <Title level={5} id={id} className="!mt-4 !mb-3 scroll-mt-20">
                  {children}
                </Title>
              )
            },
            img: ({ src, alt }) => <Image src={src} alt={alt} className="rounded-lg shadow-md" style={{ width: '100%' }} />,
            p: ({ children }) => <Typography.Paragraph className="!mb-4 !text-base">{children}</Typography.Paragraph>,
            ul: ({ children }) => <ul className={`list-disc list-inside !pl-4 !my-3 space-y-2 text-base ${isDark ? 'text-gray-300' : ''}`}>{children}</ul>,
            ol: ({ children }) => <ol className={`list-decimal !pl-4 !my-3 space-y-2 text-base ${isDark ? 'text-gray-300' : ''}`}>{children}</ol>,
            code: ({ className, children }) => {
              const match = /language-(\w+)/.exec(className || '')
              const language = match ? match[1] : ''
              const isInline = !className

              if (isInline) {
                return (
                  <code className={`px-1.5 py-0.5 rounded text-sm font-mono ${isDark ? 'bg-[#2a2a2a] text-gray-200' : 'bg-gray-100 text-gray-800'}`}>
                    {children}
                  </code>
                )
              }

              return <CodeBlock language={language}>{String(children)}</CodeBlock>
            },
            blockquote: ({ children }) => (
              <blockquote className={`p-6 [&>div:last-child]:!mb-0 mx-0 my-6 rounded-lg shadow-sm ${isDark ? 'bg-[#2a2a2a]' : 'bg-orange-50'}`}>
                {children}
              </blockquote>
            ),
            a: ({ href, children }) => {
              // 
              const isExternal = href?.startsWith('http://') || href?.startsWith('https://')
              return isExternal ? (
                <a
                  href={href}
                  target="_blank"
                  rel="noopener noreferrer"
                  className={`underline ${isDark ? 'text-blue-400 hover:text-blue-300' : 'text-blue-600 hover:text-blue-800'}`}
                >
                  {children}
                </a>
              ) : (
                <a href={href} className={`underline ${isDark ? 'text-blue-400 hover:text-blue-300' : 'text-blue-600 hover:text-blue-800'}`}>
                  {children}
                </a>
              )
            },
            table: ({ children }) => (
              <div className="overflow-x-auto my-6">
                <table className={`w-full border-collapse border rounded-lg overflow-hidden ${isDark ? 'border-gray-700' : 'border-gray-200'}`}>
                  {children}
                </table>
              </div>
            ),
            thead: ({ children }) => <thead className={isDark ? 'bg-[#2a2a2a]' : 'bg-gray-50'}>{children}</thead>,
            tbody: ({ children }) => <tbody className={`divide-y ${isDark ? 'divide-gray-700' : 'divide-gray-200'}`}>{children}</tbody>,
            tr: ({ children }) => <tr className={`transition-colors ${isDark ? 'hover:bg-[#2a2a2a]' : 'hover:bg-gray-50'}`}>{children}</tr>,
            th: ({ children }) => (
              <th
                className={`px-4 py-3 text-left text-sm font-semibold border-b ${isDark ? 'text-gray-200 border-gray-700' : 'text-gray-700 border-gray-200'}`}
              >
                {children}
              </th>
            ),
            td: ({ children }) => (
              <td className={`px-4 py-3 text-sm border-b ${isDark ? 'text-gray-300 border-gray-700' : 'text-gray-600 border-gray-100'}`}>{children}</td>
            ),
          }}
        >
          {content}
        </ReactMarkdown>

        {/*  */}
        <PageNavigation prev={pageNav.prev} next={pageNav.next} />
      </div>
    </div>
  )
}

export default MarkdownPage
